﻿@keyframes topToDown {
    0% {
        transform: translateX(-200%);
    }

    100% {
        transform: translateX(0);
    }
}

@keyframes downToUp {
    0% {
        transform: translateX(0);
    }

    100% {
        transform: translateX(-200%);
    }
}


$bg-blue: #0da3e2;
$xs-height:60px;

div.header {
    z-index: 999;
    position: relative;
    box-shadow: 0 1px 3px #ddd;
    background: #fff;
    filter: none;
    border: none;
    padding-left: 0;
    padding-right: 0;

    > .nav-container {
        margin-right: auto;
        margin-left: auto;

        &:before, &:after {
            content: " ";
            display: table;
        }

        display: flex;
        flex-direction: row;
        flex-direction: column;

        > {
            .nav-header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0;

                .nav-brand {
                    transition: padding 0.3s ease-in-out;
                    position: relative;
                    z-index: 2;
                    color: #777;
                    float: left;
                    font-size: 18px;
                    line-height: 20px;
                    margin: 15px;
                    >img {
                        width: 32px;
                        margin-top: -4px;
                    }
                }

                .nav-toggle {
                    display: flex;
                    cursor: pointer;
                    background-color: $bg-blue;
                    border: none;
                    padding: 10px;
                    color: #fff;
                    font-size: 14px;
                    outline: none;
                    border-radius: 0;
                    text-shadow: none;
                    position: relative;
                    float: right;
                    margin-right: 15px;
                    cursor: pointer;
                }
            }

            .nav-menu {
                display: flex;
                flex-grow: 1;
                position: relative;
                border-color: #e7e7e7;
                max-height: 340px;
                align-items: center;

                > .search-container {
                    position: relative;
                    flex-direction: row;
                    align-items: center;

                    span.split {
                        background-color: #eee;
                        width: 1px;
                        height: 32px;
                        margin-right: 12px;
                        margin-left: 5px;
                    }

                    a {
                        display: flex;
                        color: #bbb;
                        cursor: pointer;
                    }

                    .search-box {
                        display: none;
                        position: absolute;
                        top: 100%;
                        right: 0;
                        padding: 10px;
                        background: #fff;
                        position: absolute;
                        border: solid 1px #eee;
                        border-top: none;
                        margin-top: 2px;
                        width: 340px;

                        &.active {
                            display: flex;
                        }


                        form {
                            width: 100%;
                            display: flex;
                            flex-direction: row;

                            > {
                                .search-val {
                                    flex-grow: 1;
                                    padding: 6px 12px;
                                    color: #333333;
                                    background-color: #ffffff;
                                    border: 1px solid #e5e5e5;
                                    font-size: 14px;
                                    font-weight: normal;

                                    &::placeholder {
                                        color: #bbb;
                                    }

                                    &:focus {
                                        border: 1px solid #888;
                                        outline: none;
                                    }
                                }

                                .btn-search-submit {
                                    color: #fff;
                                    background-color: $bg-blue;
                                    border-radius: 0;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

.nav-menu ul.nav {
    list-style: none;
    align-content: space-between;

    &:before, &:after {
        content: " ";
        display: table;
    }

    li {
        display: flex;
        flex-direction: row;
        align-items: center;
        position: relative;
        cursor: pointer;

        a {
            display: flex;
            align-items: center;
            box-sizing: border-box;
            color:#333;
        }

        &.active {
            > a {
                background: #fcfcfc;
                color: $bg-blue !important;
            }

            &:hover > a {
                color: $bg-blue !important;
            }
        }

        &:hover > a {
            color: $bg-blue !important;
        }
    }

    > li.nav-dd {
        a.nav-dd-toggle {
            font-weight: 400;
            text-shadow: none;
            position: relative;
            padding: 26px 16px 27px;
            border-top: solid 2px #fff;

            > i {
                margin-left: 4px;
            }
        }

        .nav-dd-menu {
            box-shadow: none;
            min-width: 185px;

            @media(min-width:992px) and (max-width:1150px) {
                min-width: 160px;
            }

            padding: 0;
            top: 100%;
            left: 0;
            z-index: 1000;
            float: left;
            list-style: none;
            text-shadow: none;
            margin: 0px;
            font-size: 14px;
            border: 1px solid #ddd;
            background-clip: padding-box;
            display: none;
            flex-direction: column;
            background-color: #fff;

            li {
                border-bottom: solid 1px #f5f5f5;

                a {
                    padding: 9px 15px;
                    width: 100%;
                    background-color: #fcfcfc;
                }

                &.active {
                    > a, &:hover > a {
                        background-color: #fcfcfc;
                    }
                }
            }

            &.active {
                display: flex;
            }
        }
    }
}

@media (max-width: 767.8px) {
    .nav-show {
        animation: topToDown 0.3s linear;
        display: flex !important;
        animation-fill-mode: forwards;
    }

    .nav-hide {
        animation: downToUp 0.3s linear;
        animation-fill-mode: forwards;
    }

    div.header > .nav-container > .nav-menu {
        transform: translateX(-200%);
        position: absolute;
        top: 100%;
        flex-direction: column;
        width: 100%;

        ul.nav {
            background-color: #fff;
            width: 100%;

            li {
                width: 100%;
                border-bottom: 1px solid #e5e5e5;

                a {
                    border-top: none;
                    width: 100%;
                    padding: 10px 15px;
                }
            }


            > li {
                &.active {
                    > a, &:hover > a {
                        background-color: $bg-blue;
                        color: #fff !important;
                    }
                }
            }

            > li.nav-dd {
                flex-direction: column;

                > .nav-dd-toggle {
                    padding: 10px 15px;
                }

                > .nav-dd-menu {
                    padding-left: 20px;
                    width: 100%;
                    box-shadow: none;
                    position: static;
                    border:none;
                    border-top: solid 1px #eee;

                    li {
                        border-bottom: 1px solid #e5e5e5;

                        a {
                            background-color: #fff;
                        }

                        &.active {
                            > a, &:hover > a {
                                background-color: #e7e7e7;
                                color: $bg-blue !important;
                            }
                        }
                    }
                }
            }
        }

        > .search-container {
            display: none;

            .search-box {
                position: static;
            }
        }
    }
}

@media (min-width: 768px) {
    div.header > .nav-container {
        width: 750px;

        > .nav-header {
            margin: 0;
            justify-content: flex-start;

            .nav-toggle {
                display: none;
            }

            .nav-brand {
                height: 50px;
                margin: 0;
                padding: 15px 15px 0;
            }
        }

        > .nav-menu {
            justify-content: space-between;
            display: flex;
            flex-direction: row;
            transform: translateX(0);

            ul.nav,
            .search-container {
                display: flex;
            }



            > ul.nav {
                > li {
                    height: 40px;

                    > a {
                        padding: 0 15px 2px;
                        height: 40px;
                        font-size: 18px;
                        box-sizing: border-box;
                    }

                    &.nav-dd ul.nav-dd-menu {
                        padding: 0px;
                        box-shadow: 0 1px 8px rgba(0, 0, 0, 0.1);
                        position: absolute;
                        border-bottom: none;
                        border-left: solid 1px #eee;
                        border-right: solid 1px #eee;
                    }
                }
            }

            > .search-container {
                height: 40px;
            }
        }
    }
}

@media (min-width: 992px) {
    div.header > .nav-container {
        width: 970px;
        flex-direction: row;

        > .nav-header {
            .nav-brand {
                height: 50px;
                padding: 15px;

            }
        }

        > .nav-menu {
            justify-content: flex-end;

            > ul.nav {
                > li {
                    height: $xs-height;

                    > a {
                        padding: 0 20px 2px;
                        height: $xs-height;
                        box-sizing: border-box;
                        font-size: 18px;
                    }
                }

                li {
                    a {
                        border-top: solid 2px #fff;
                    }

                    &.active {
                        > a, &:hover > a {
                            border-top: solid 2px $bg-blue !important;
                        }
                    }

                    &:hover > a {
                        border-top: solid 2px $bg-blue !important;
                    }
                }

                li.active > a:after {
                    top: 0;
                    left: 50%;
                    z-index: 2;
                    content: " ";
                    display: block;
                    margin-left: -3px;
                    margin-top: 2px;
                    position: absolute;
                    border-top: 3px solid $bg-blue;
                    border-left: 3px solid transparent;
                    border-right: 3px solid transparent;
                }

                > li.nav-dd > .nav-dd-menu {
                    border-top: solid 2px $bg-blue;
                }
            }

            > .search-container {
                height: 100%;
            }
        }
    }
}

@media (min-width: 1200px) {
    div.header > .nav-container {
        width: 1170px;
    }
}

